
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    showTitle: {
        type: Boolean,
        default: true
    },
    enabled: {
        type: Boolean,
        default: true
    },
    color: {
        type: String
    },
    label: {
        type: String,
        required: true
    },
    value: {
        type: String,
        required: true
    }
  },
  computed: {
      state(): string {
          if (this.enabled) return `${this.color} clickable`

          return 'disabled-card-color'
      }
  },
  methods: {
    onclick() { if (this.enabled) this.$emit('onclick') }
  }  
})
